import React from "react";
import { Link } from "react-router-dom";
import "./HomePage.css";

// Import Carousel and its CSS
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from "react-responsive-carousel";

// Import your slider images
// import slider1 from "../assets/ganesha.jpg";
// import slider2 from "../assets/shiva.jpg";
// import slider3 from "../assets/vishnu.jpg";
import slider1 from "../assets/slider1.jpg";
import slider2 from "../assets/slider2.jpg";
import slider3 from "../assets/slider3.jpg";

function HomePage() {
  return (
    <div className="home-page">
      {/*   */}
      <section className="hero-slider">
        <Carousel
          autoPlay
          infiniteLoop
          showThumbs={false}
          showStatus={false}
          interval={5000}
        >
          <div>
            <img src={slider1} alt=" " />
          </div>
          <div>
            <img src={slider2} alt=" " />
          </div>
          <div>
            <img src={slider3} alt=" " />
          </div>
        </Carousel>
      </section>

      {/*   */}
      <section className="container welcome-section">
        <h2> - </h2>

        <p className="main-description">
               .   
           ,         
           .  ,      
          .
        </p>

        <div className="features">
          <div className="feature">
            <span className="icon"></span>
            <h3></h3>
            <p>   </p>
          </div>
          <div className="feature">
            <span className="icon"></span>
            <h3></h3>
            <p>  </p>
          </div>
          <div className="feature">
            <span className="icon"></span>
            <h3></h3>
            <p>   </p>
          </div>
        </div>
      </section>
    </div>
  );
}

export default HomePage;
